
html,body{
    width:100%;
    height:100%;
    margin:0;
    padding:0;
    overflow: hidden;
}
#app{
  width:100%;
  height:100%;
  /*background:pink;*/
}

.bubbleClass {
  width: 222px;
  height: 222px;
  position: absolute;
  top: 30px;
}
.bubbleClass > div {
  width: 222px;
  height: 222px;
  position: absolute;
  /*background:url('../assets/images/red.png')*/
}
.bubbleClass > div:nth-child(1) {
  background: url(../../static/img/red.b7411a5.png);
}
.bubbleClass > div:nth-child(2) {
  background: url(../../static/img/green.7f72780.png);
}
.bubbleClass > div:nth-child(3) {
  background: url(../../static/img/blue.4a78c8f.png);
}
.bubbleClass > div:nth-child(4) {
  background: url(../../static/img/background.0b8a2e5.png);
}
#topButton {
  padding: 10px 20px;
  border-radius: 3px;
  border: 0 solid #888;
  margin: 0 auto;
}
#topButton:hover {
  cursor: pointer;
}
